<template>
  <!-- App.vue -->

  <v-app>
    <v-navigation-drawer
      app
      width="500px"
      v-model="drawer"
      :color="headerColorHex"
    >
      <v-btn style="margin: 4px; width: 56px; height: 56px;" @click="drawer = !drawer" color="white" icon><v-icon >mdi-close</v-icon></v-btn>
      <v-card :elevation="0" style="margin-bottom: 100px;">
        <!-- Provides the application the proper gutter -->
        <v-card
          v-for="type of types" :key="type"
          :dark="onecolor(selected[type].hex).lightness() < lightThreshold"
          outlined
          tile
          :style="{ backgroundColor: selected[type].hex }"
          class="pa-4"
        >
          <v-card-title>{{ capitalize(type) }}</v-card-title>
          <v-card-text
            class="pa-4"
          >
            <ColorAutocompleteComponent
              :colors="colors"
              v-model="selected[type]"
            ></ColorAutocompleteComponent>
          </v-card-text>
        </v-card>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn :color="headerColorHex" :dark="dark" @click="save">Save</v-btn>
        </v-card-actions>
        <v-divider class="my-8 mx-4"></v-divider>
        <div class="headline text-center">Saved Palattes</div>
        <v-divider class="my-8 mx-4"></v-divider>
        <v-list-item v-for="sp in savedPalattes" @click="selected = sp" :key="sp.base.hex">
          <v-list-item-avatar class="sp">
            <div class="sp--base" :style="{ backgroundColor: sp.base.hex }"></div>
            <div class="sp--white" :style="{ backgroundColor: sp.white.hex }"></div>
            <div class="sp--accent" :style="{ backgroundColor: sp.accent.hex }"></div>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{ sp['base'].name }}/{{ sp['white'].name }}/{{ sp['accent'].name }}</v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-btn icon @click="remove(sp)">
              <v-icon color="red lighten-1">mdi-delete</v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
      </v-card>
    </v-navigation-drawer>

<!--
  
  <ColorGridComponent
    v-for="fam of colorFamilies"
    :colorfam="fam"
    :key="fam"
    class="mr-2"
  ></ColorGridComponent>
 -->
    <v-app-bar app :color="headerColorHex" :dark="dark">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-btn icon><v-icon @click="randomize">mdi-refresh</v-icon></v-btn>
    </v-app-bar>

    <!-- Sizes your content based upon application components -->
    <v-main>
      <v-container fluid class="fill-height pa-0">
        <PalatteViewComponent
          :colors="selected"
        ></PalatteViewComponent>
      </v-container>
    </v-main>

    <v-footer app :color="headerColorHex">
      <!-- -->
    </v-footer>
  </v-app>
</template>

<style>
.sp > div {
  height: 2em;
  width: 6em;
}
</style>

<script>
import colors from './colors.json';
import { capitalize } from './lib/utils';
import onecolor from 'onecolor';
import ColorAutocompleteComponent from './components/ColorAutocompleteComponent.vue';
import PalatteViewComponent from './components/PalatteViewComponent.vue';
// import ColorGridComponent from './components/ColorGridComponent.vue';

export default {
  name: 'App',

  components: { ColorAutocompleteComponent, PalatteViewComponent, },//ColorGridComponent },

  created(){
    colors.sort((a,b) => a.name > b.name ? 1 : -1);

    this.byFam = this.colors.reduce((acc, c) => {
      acc[c.fam] = acc[c.fam] || []
      acc[c.fam].push(c);
      return acc;
    }, {});
    try {
      if (localStorage.getItem('sp')) {
        this.savedPalattes = JSON.parse(localStorage.getItem('sp'));
      }
    } catch(e) {
      console.error(e);
    }
    const { base, white, accent } = this.$route.query;
    if (base && white && accent) {
      this.selected.base = this.colors.find(c => c.name == base);
      this.selected.white = this.colors.find(c => c.name == white);
      this.selected.accent = this.colors.find(c => c.name == accent);
    } else {
      this.randomize();
    }
  },

  data: () => ({
    savedPalattes: [],
    colorFamilies: [
      'red',
      'orange',
      'yellow',
      'green',
      'blue',
      'purple',
      'gray',
      'neutral',
      'white',
    ],
    colors,
    drawer: false,
    log: console.log,
    types: [
      'base',
      'white',
      'accent',
    ],
    selected: {
      base: {
        hex: '',
        name: '',
      },
      white: {
        hex: '',
        name: '',
      },
      accent: {
        hex: '',
        name: '',
      },
    },
    lightThreshold: 0.5,
  }),
  methods: {
    capitalize,
    onecolor,
    randomize() {
      do {
        this.selected['base'] = this.colors[Math.floor(Math.random() * this.colors.length)];
      } while(onecolor(this.selected['base'].hex).lightness() > 0.5)
      do {
        this.selected['accent'] = this.byFam['red'][Math.floor(Math.random() * this.byFam['red'].length)];
      } while(onecolor(this.selected['accent'].hex).lightness() > 0.5)

      this.selected['white'] = this.byFam['white'][Math.floor(Math.random() * this.byFam['white'].length)];
    },
    save() {
      this.savedPalattes.push(JSON.parse(JSON.stringify(this.selected)));
      localStorage.setItem('sp', JSON.stringify(this.savedPalattes));
    },
    remove(item) {
      this.savedPalattes.splice(this.savedPalattes.indexOf(item), 1);
      localStorage.setItem('sp', JSON.stringify(this.savedPalattes));
    }
  },
  computed: {
    headerColor() {
      const color = onecolor(this.selected['base'].hex || '#f00');
      return color.lightness(color.lightness() * 0.5);
    },
    headerColorHex() {
      return this.headerColor.hex();
    },
    dark() {
      return this.headerColor.lightness() < this.lightThreshold;
    },
  }
};
</script>
